@import 'assets/scss/utils/mixins';

a {
	text-decoration: none;
	cursor: pointer;
}

.background-image {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
}

.header-tabs {
	height: 60px;
}

div.filters {
	width: 100%;
	margin: auto;
	max-width: 708px;

	&--share {
		max-width: 488px;
	}
}

.close-btn {
	width: 30px;
	height: 30px;
}

.fa-solid {
	color: var(--basic-1);
}

.home-filters {
	position: relative;
	min-height: 375px;

	img {
		object-fit: cover;
		z-index: var(--z-index-home-hero-banner);
	}
}

@include media-breakpoint-up(md) {
	.header-tabs {
		max-width: 1068px;
	}

	.background-image {
		height: 350px;
	}

	.website-logo {
		font-size: 32px !important;
		line-height: 36px !important;
	}
}
.menu-header {
	background: none;
	border: none;
	left: 10px;
	position: absolute;
}
.icn-menu {
	color: var(--general-1) !important;
}

.banner-gradient {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
  // background: linear-gradient(180deg, hsla(209, 93%, 12%, 1) 16%, hsla(185, 37%, 34%, 1) 86%);
  background: linear-gradient(0deg, rgba(26, 31, 46, 0.75) 0%, rgba(26, 31, 46, 0.75) 100%), rgba(238, 0, 136, 0.15);
  min-height: 200px;

  z-index: -1;

	@include media-breakpoint-up(md) {
		min-height: 300px;
	}
}
